/* General Styles */
body {
  font-family: sans-serif;
  background-color: rgb(192,192,192);
  color: #333;
  margin: 0;
}

.App {
  text-align: center;
  min-height: 100vh;
}

.logo-container {
  max-width: 100vw;
  min-width: 320px;
  margin: 0 auto;
  padding: 2rem 0;
  text-align: center;
}

.nav-container {
  max-width: 100vw;
  min-width: 320px;
  margin: 0 auto;
  padding: 1rem 0;
  text-align: center;
  background-color: rgba(212,175,55,0.6);
}

/* Header Styles */
header {
  max-width: 100vw;
  min-width: 320px;
  background-color: rgba(0,0,0,0.9);
  border-bottom: 1px solid #ccc;
  padding: 1rem 0;
}

.logo-image {
  width: 80vw;
  height: auto;
  filter: drop-shadow(0px 0px 10px gold);
}

/* Navigation Styles */
nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  display: inline-block;
  margin-left: 2.5vw;
  margin-right: 2.5vw;
  position: relative;
  text-align: center;
}

nav a {
  color: #333;
  text-decoration: none;
}

nav a:hover {
  color: rgb(183,110,121);
}

/* Remove the old pseudo-element arrow */
.dropdown-toggle::after {
  display: none;
}

/* Style the new v-shaped arrow */
.dropdown-arrow {
  display: inline-block;
  margin-left: 5px;
  font-size: 0.6em;
  transform: scaleY(0.6); /* Make it more v-shaped */
  position: relative;
  top: -2px; /* Adjust vertical alignment */
  color: inherit;
}

/* Hide arrow on hover or when menu is open */
.dropdown-toggle:hover .dropdown-arrow,
.dropdown-toggle.active .dropdown-arrow {
  display: none;
}

/* Submenu Styles */
.submenu {
  display: none;
  position: absolute;
  top: 180%;
  left: 0;
  background-color: rgba(212,175,55,0.3);
  border: 1px solid #ccc;
  padding: 1rem;
  z-index: 999;
  min-width: 200px;
}

/* Show submenu on hover or when active */
nav ul li:hover .submenu,
nav ul li.active .submenu {
  display: block;
}

.submenu li {
  display: block;
  margin: 10px 0;
  transition: background-color 0.3s ease;
}

.submenu li:hover {
  background-color: rgba(183,110,121,0.6);
}

.submenu li a {
  color: rgb(183,110,121);
  text-align: center;
  display: block;
  padding: 5px 10px;
}

.submenu li:hover a {
  color: rgba(212,175,55,0.8);
}

/* Features Section Styles */
.features {
  margin-top: 50px;
}

.features .container {
  display: flex;
  justify-content: space-between;
}

.features .column {
  flex: 1;
  padding: 2rem 0;
  text-align: center;
}

/* Footer Styles */
footer {
  background-color: #333;
  color: #fff;
  padding: 2rem 0;
  margin-top: 50px;
}

footer .container {
  display: flex;
  justify-content: space-between;
  margin-left: 1vw;
}

footer .copyright {
  margin-left: 1vw;
}

footer .column {
  flex: 1;
}

footer ul.social-media {
  list-style: none;
  margin: 0;
  padding: 0;
}

footer ul.social-media li {
  display: inline-block;
  margin-right: 10px;
}

footer ul.social-media a {
  color: #fff;
  text-decoration: none;
}

#meta {
  height: 1.5em;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .features .container {
    flex-direction: column;
    align-items: center;
  }
}

footer a {
  color: #fff;
  text-decoration: none;
  transition: opacity 0.3s ease;
}

footer a:hover {
  opacity: 0.64;
}

