:root {
  --metallic-gold: linear-gradient(145deg, #ffd700, #b8860b);
  --metallic-silver: linear-gradient(145deg, #c0c0c0, #808080);
  --card-base-size: min(400px, 40vw); /* Responsive base size */
}

.carousel-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 0;
  overflow: hidden;
}

.carousel-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  perspective: 1200px;
  position: relative;
  height: calc(var(--card-base-size) + 50px);
  width: 100%;
  max-width: 100vw;
}

.carousel {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel-card {
  position: absolute;
  transform-origin: center center;
  transition: all 0.5s ease-out;
  width: var(--card-base-size);
  height: var(--card-base-size);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

.carousel-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.carousel-card.active {
  filter: drop-shadow(0px 0px 25px gold);
  border: 2px solid #ffd700;
}

.carousel-button {
  background: rgb(183,110,121);
  border: none;
  font-size: clamp(16px, 2vw, 48px);
  cursor: pointer;
  padding: clamp(5px, 0.8vw, 20px);
  color: silver;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  border-radius: 50%;
  width: clamp(30px, 3.5vw, 60px);
  height: clamp(30px, 3.5vw, 60px);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.carousel-button:hover {
  opacity: 0.64;
  color: rgba(212,175,55,0.64);
  transform: translateY(-50%) scale(1.1);
}

.carousel-button:active {
  opacity: 0.64;
  color:rgba(212,175,55,0.64);
  transform: translateY(-50%) scale(0.95);
}

.carousel-button.prev {
  left: max(2px, 2vw);
}

.carousel-button.next {
  right: max(2px, 2vw);
}

/* Media Queries */
@media (max-width: 1400px) {
  :root {
    --card-base-size: min(250px, 30vw);
  }
}

@media (max-width: 1024px) {
  :root {
    --card-base-size: min(200px, 35vw);
  }
}

@media (max-width: 768px) {
  :root {
    --card-base-size: min(180px, 40vw);
  }
}

@media (max-width: 480px) {
  :root {
    --card-base-size: min(150px, 45vw);
  }
  
  .carousel-container {
    padding: 10px 0;
  }
}

/* Additional media query for extremely narrow screens */
@media (max-width: 400px) {
  .carousel-container {
    margin-bottom: 35px;
  }

  .carousel-button {
    width: 25px;
    height: 25px;
  }

  .carousel-button.prev {
    left: 40vw;
  }
  
  .carousel-button.next {
    right: 40vw;
  }
  
}

/* Update App background */
.App {
  background: var(--metallic-silver);
} 